import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-order-status',
  templateUrl: './order-status.component.html',
  styleUrls: ['./order-status.component.scss']
})
export class OrderStatusComponent implements OnInit {
  searchValue: any;
  salesOrder: Array<any> = []
  salesSubOrder: Array<any> = []
  list_status_bill: Array<any> =
    ["All", "Wait for confirmation", "Order delay", "Cancelled(not delivery yet)", "ready_to_pick",
      "Shipping", "Delivery failed", "Canceled (return goods)", "Customer received", 'Done', "Wait for separation", "Wait for merger"]
  heightScroll: any;
  widthScroll: any;
  public date: Date[] = [new Date(), new Date()];
  load_status: boolean = false;
  dataRestore: any = {};
  selectedIndex: number = 0;
  isVisibleConfirm: boolean = false
  loadingCancel: boolean = false
  loadingConfirm: boolean = false
  billSelected: any = {}
  /**Giới hạn thời gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  infoShop: any = {}
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private vhAuth: VhAuth
  ) {
    this.dataRestore = this.router.getCurrentNavigation().extras.state?.dataRestore
  }

  ngOnInit(): void {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.salesOrder = this.dataRestore.salesOrder
      this.salesSubOrder = this.dataRestore.salesSubOrder
      this.load_status = true;
      this.selectedIndex = this.dataRestore.selectedIndex
      this.date = this.dataRestore.date;
      this.segmentChanged(this.selectedIndex)
    }
    this.infoShop = this.vhQuerySales.getlocalShopListSendo()[0];
    this.salesOrder = []
    this.salesSubOrder = []

  }

  /**
   * lọc salesOrder theo order_number
   * @param value 
   * @example this.search('060623A001')
   */
  search(value: string) {
    this.searchValue = value;
    this.segmentChanged(this.selectedIndex)
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
    this.salesOrder = this.vhAlgorithm.searchList(val, [...this.salesOrder], ['order_number'])

  }
  
  /**
   * bắt sk thay đổi tab xem trạng thái đơn
   * @param event 
   */
  segmentChanged(event) {
    this.selectedIndex = event
    this.searchValue = ''
    if (event == 0) this.salesOrder = [...this.salesSubOrder]
    else this.salesOrder = this.salesSubOrder.filter(item => item.segmentStatus == event)
  }
  /**
   * trở về trang quản lý sendo
   * @example this.goBack()
   */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/sendo'], {
      state: { dataRestore: this.dataRestore }
    })
  }
  /**
   * bắt sk thay đổi ngày xem hóa đơn
   * @example this.changeTime('2023-06-09T04:14:30.367Z') 
   */
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 2678400000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + 2678313600)]
    }
  }
  /**
   * get hóa đơn sendo
   * @example this.showReport()
   */
  showReport() {
    this.load_status = true;
    this.salesOrder = []
    this.salesSubOrder = []
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getSalesOrderDetailListSendo(new Date(this.date[0].setHours(0, 0, 0)).toISOString(), new Date(this.date[1].setHours(23, 59, 59, 59)).toISOString(), this.infoShop.token, this.infoShop._id).then((result: any) => {
        this.vhComponent.hideLoading(0)
        result.forEach(item => {
          let statusSegment: Array<any> = this.renderSegmentStatus(item.sales_order);
          this.salesOrder.push({
            ...item.sales_order, sku_details: item.sku_details,
            segmentStatus: statusSegment[0],
            status_on_sendo: statusSegment[1],
            created_date_time_stamp: new Date((item.sales_order.created_date_time_stamp - 7 * 60 * 60) * 1000), id_shop: this.infoShop._id,
          })
        })
        this.salesSubOrder = [...this.salesOrder];
        this.segmentChanged(this.selectedIndex)
      }).catch(err => this.vhComponent.hideLoading(0))
    })
  }
  
  /**
   * trả về tên trạng thái đơn trên sendo
   * @param order 
   * @returns string
   * @example let status_name = this.renderSegmentStatus(order_sendo)
   */
  renderSegmentStatus(order) {
    switch (order.order_status) {
      case 2:
        if ([0, 1, 2].includes(order.delivery_status)) return [1, "Wait for confirmation"];break;
      case 3: if ([0].includes(order.delivery_status)) return [4, "Looking for transporter"];break;
      case 6: {
        if ([4, 5].includes(order.delivery_status) && order.is_delivery_failed)
          return [6, "Delivery failed"]
        else if ([4, 5].includes(order.delivery_status) && order.is_delivery_failed == false) {
          if (order.verify_status_delivered_goods) return [5, "Delivering (shipper already delivered)"]
          else return [5, "Delivering (not yet delivered shipper)"]
        }
        else if ([3, 14, 15].includes(order.delivery_status)) return [4, "ready_to_pick"];break;
      }
      case 7: {
        if ([6].includes(order.delivery_status) && [2, 3].includes(order.payment_status) && !order.is_disputing) return [8, "Delivered"]
        if ([6].includes(order.delivery_status) && [2, 3].includes(order.payment_status) && order.is_disputing) return [0, "Complaint pending"]
        ;break;
      }
      case 8: {
        if ([6].includes(order.delivery_status) && order.payment_status == 2) return [9, "Wait to receive money"]
        else if ([6].includes(order.delivery_status) && order.payment_status == 4 && !order.refund_trans) {
          if (order.verify_status_payment)
            return [9, "Complete (the seller has received the money)"]
          else return [9, "Complete (the seller has not received the money)"]
        }
        else if ([6].includes(order.delivery_status) && order.payment_status == 4 && order.refund_trans) return [0, "Close"]
        ;break;
      }
      case 10: if ([6].includes(order.delivery_status) && [4, 5].includes(order.payment_status)) return [1, "Close"];break;
      case 11: case 12:
        if ([0].includes(order.delivery_status)) return [2, "Order delay"];break;
      case 13: {
        if ([0, 3].includes(order.delivery_status)) return [3, "Cancelled(not delivery yet)"]
        else if ([8].includes(order.delivery_status)) {
          if (order.verify_status_received_goods) return [7, "Return the goods to the seller (checked return goods)"]
          else if (!order.verify_status_received_goods) return [7, "Return the goods to the seller (not checked return goods yet)"]
        }
        else if ([10].includes(order.delivery_status)) {
          if (order.verify_status_received_goods) return [7, "The seller receives the goods (checked return goods)"]
          else if (!order.verify_status_received_goods) return [7, "The seller receives the goods (not checked return goods yet)"]
        }
        else if ([16].includes(order.delivery_status)) {
          if (order.verify_status_received_goods) return [7, "Lost goods"]
          else if (!order.verify_status_received_goods) return [7, "Lost goods"]
        };break;
      }
      case 14: case 15: if ([0].includes(order.delivery_status)) return [11, "Wait for separation"];break;
      case 19: if ([0].includes(order.delivery_status)) return [11, "Wait for merger"];break;
      case 21:
        if ([11, 12].includes(order.delivery_status) || [11, 12].includes(order.delivery_status) && order.is_disputing) return [0, "Returning goods"]
        ;break;
      case 22:
        if ([13].includes(order.delivery_status)) return [0, "Successfully return"];break;
      default: return [0, '']
    }
  }
  ngAfterViewChecked() {
    if (document.querySelector("#order-status-sendo") && document.querySelector(".ant-tabs-tab") && document.querySelector(".ant-table-thead") && document.querySelector(".order-status-sendo-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#order-status-sendo").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".order-status-sendo-header").clientHeight - document.querySelector(".ant-tabs-tab").clientHeight - 90 + "px";
    }
    if (document.getElementById("order-status-sendo"))
      this.widthScroll = document.getElementById("order-status-sendo").clientWidth + "px"
    this.cdRef.detectChanges();
  }

  gotoDetail(bill) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/sendo/order-status/detail'], { state: { bill: bill, dataRestore: { salesOrder: this.salesOrder, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesSubOrder: this.salesSubOrder, selectedIndex: this.selectedIndex } } })
  }

  /**
   * mở modal xác nhận đơn hàng cho đơn được chọn
   * @param bill 
   * @example this.openModalConfirm(bill
   */
  openModalConfirm(bill: any) {
    this.billSelected = bill
    this.isVisibleConfirm = true
  }

  /**
   * cập nhật trạng thái đơn hàng trên sendo
   * @param order_status 3 || 13
   * @example this.confirmPacked(13)
   */
  confirmPacked(order_status: number) {
    order_status == 13 ? this.loadingCancel = true : this.loadingConfirm = true
    this.vhQuerySales.updateOrderStatusSendo(this.billSelected['order_number'], order_status, order_status == 13 ? 'CBS1' : null, this.infoShop.token).then(res => {
      if (res) {
        this.vhComponent.alertMessageDesktop('success', this.lang.translate('Xác nhận đơn thành công'))
        this.billSelected['order_status'] = order_status
        if (order_status == 13) this.billSelected['status_on_sendo'] = 'Cancelled(not delivery yet)'
        else this.billSelected['status_on_sendo'] = 'Looking for transporter'
      } else this.vhComponent.alertMessageDesktop('error', this.lang.translate('Cập nhật thất bại, vui lòng thử lại'))
      order_status == 13 ? this.loadingCancel = false : this.loadingConfirm = false
      this.isVisibleConfirm = false
    }).catch(err => {
      
      this.vhComponent.alertMessageDesktop('error', this.lang.translate('Cập nhật thất bại, vui lòng thử lại'))
    })
  }
}
